.nav {
    height: 5vh;
    font-size: 14px;
}

.menu {
    float: right;
    font-family: 'Roboto', sans-serif;
    display: table;
    margin-right: 5vh;
}

.nav li {
    list-style-type: none;
    display: table-cell; 
    vertical-align: middle;
}

li:nth-of-type(1){
    padding-right: 25px;
}

.sidebar {
    position: fixed;
    bottom: 50vh;
    right: 0; 
    display: flex; 
    flex-direction: column;
    justify-content: center;
}

.contactLogo {
    width: 20px; 
    display: block; 
    padding-bottom: 2px;
    padding-top: 2px;
    padding-right: 10px;
}

.linkedin {
    padding-bottom: 5px;
}
.main{
    background-color: rgb(218, 218, 218);
    height: 90vh;
    margin-left: 5vh;
    margin-right: 5vh;
}
